import { h } from 'preact';
import { useEffect, useState, useContext } from 'preact/hooks';
import { route } from 'preact-router';
import { Link } from 'preact-router/match';

import { AuthContext } from "../../components/Auth";
import Error_MSG from '../../components/Error_MSG';
import Success_MSG from '../../components/Success_MSG';
import style from './style.css';

const Validate = () => {

  const { currentUser } = useContext(AuthContext);
  const [buttonLoading, setButtonLoading] = useState(false);
  const [activeRoute, setActiveRoute] = useState('index.html');
  const [key, setKey] = useState('');
  const [validateError, setValidateError] = useState({error: false, msg: ''});
  const [validateMSG, setValidateMSG] = useState({success: false, msg: ''});

  useEffect(() => {
    const handleRoute = async () => {
      // Redirect to home on success.

      let queryOptions = { active: true, currentWindow: true };
      let [tab] = await chrome.tabs.query(queryOptions);

      const regex = /admin\/menus\/[0-9]+(\?.*)?$/;
      const regex1 = /store\/.*\/menus\/[0-9]+(\?.*)?$/;
      const regex2 = /admin\/menus\/?(\?.*)?$/;
      const regex3 = /store\/.*\/menus\/?(\?.*)?$/;
      //console.log(tab);

      if (regex.test(tab.url) || regex1.test(tab.url)) {
        setActiveRoute('/menu');
      } else if (regex2.test(tab.url) || regex3.test(tab.url)) {
        setActiveRoute('/menus');
      } else {
        //console.log('%cIn Else Block:', 'color:blue;background:red;padding:5px;');
      }
    };

    handleRoute();
  });

  useEffect(() => {
    //console.log('%cThis is currentUser:', 'color:#FFF;background:#000;padding:5px;', currentUser);
    if (currentUser) {
      //console.log(`[Redirecting] redirect to ${activeRoute}...`);
      route(activeRoute);
    }

  }, [currentUser]);

  useEffect(() => {
    setValidateError({ error: false, msg: '' });
  }, []);

  const handleKeyChange = event => {
    setValidateError({ error: false, msg: '' });
    setKey(event.target.value);
  };

  const validateHandler = async () => {
    setButtonLoading(true);

    const cookies = await window.chrome.cookies.getAll({ domain: 'shopify.com' });
    const { value } = cookies.find(cookie => cookie.name === 'master_device_id');

    const data = {
      key,
      master_device_id: value
    };

    // POST to session login endpoint.
    return fetch('https://meraki-apps.com/validateKey', {
      method: "POST",
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data)
    })
    .then((resp) => resp.json())
    .then((data) => {
      setButtonLoading(false);

      if (data.success) {
        setValidateMSG({ 
          success: true,
          msg: [
            'Key validated! Your account is enabled.',
            <Link key="1" href="/login">Log in</Link>
          ]
        });
      } else if (data.error) {
        setValidateError({ error: true, msg: data.message || 'Failed to validate key!' });
        setTimeout(() => {
          setValidateError({ error: false, msg: '' });
        }, 4000);
      }
    })
    .catch((error = {}) => {
      //console.log(error);
      setButtonLoading(false);
      setValidateError({ error: true, msg: error.message || 'Failed to validate key! Try again.' });
      setTimeout(() => {
        setValidateError({ error: false, msg: '' });
      }, 4000);
    })
  };

	return (
		<div class={style.body}>
      <h2>Shopify Navigation Tool</h2>
      <h5>An absolute time saver</h5>

      <h4 class={style.bottom}>Validate License Key</h4>
      <input
        type="text"
        class={style.input}
        placeholder="License key"
        onChange={handleKeyChange}
      />
      <div class={style.actions}>
        <button
          class={`${buttonLoading ? style.loading : ''}`}
          onClick={validateHandler}
        >
          <svg
            height='16'
            width='16'
            fill="#000"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 32 32"
          >
            <title>Validate</title>
            <g>
              <path d="M28.35,12.35a1.49,1.49,0,0,1-1-.9,1.51,1.51,0,0,1,.1-1.34,3.53,3.53,0,0,0-4.45-5A1.52,1.52,0,0,1,20.89,4a3.53,3.53,0,0,0-6.64-1,1.48,1.48,0,0,1-1.06.82,1.5,1.5,0,0,1-1.3-.33A3.52,3.52,0,0,0,6.18,7,1.53,1.53,0,0,1,4.7,8.85h0a3.52,3.52,0,0,0-2.09,6.36,1.52,1.52,0,0,1,.62,1.19,1.5,1.5,0,0,1-.55,1.22A3.52,3.52,0,0,0,5.1,23.86a1.51,1.51,0,0,1,1.59,1.8,3.53,3.53,0,0,0,5.91,3.19,1.53,1.53,0,0,1,2.38.36A3.46,3.46,0,0,0,18,31a3.61,3.61,0,0,0,.73-.07,3.48,3.48,0,0,0,2.8-3.07A1.53,1.53,0,0,1,23.6,26.6a3.53,3.53,0,0,0,4.17-5.26A1.53,1.53,0,0,1,27.59,20a1.51,1.51,0,0,1,.94-1,3.53,3.53,0,0,0-.18-6.71Zm-.49,4.82a3.52,3.52,0,0,0-1.75,5.28,1.53,1.53,0,0,1-1.81,2.28,3.49,3.49,0,0,0-3.08.29,3.53,3.53,0,0,0-1.66,2.62,1.52,1.52,0,0,1-2.84.58,3.51,3.51,0,0,0-2.55-1.75,3.23,3.23,0,0,0-.53,0,3.49,3.49,0,0,0-2.43,1A1.52,1.52,0,0,1,8.66,26,3.53,3.53,0,0,0,5,21.86a1.52,1.52,0,0,1-1.07-2.7,3.47,3.47,0,0,0,1.28-2.81A3.51,3.51,0,0,0,3.75,13.6a1.49,1.49,0,0,1-.55-1.71,1.51,1.51,0,0,1,1.46-1A3.53,3.53,0,0,0,8.12,6.49,1.52,1.52,0,0,1,10.59,5a3.52,3.52,0,0,0,3,.77A3.48,3.48,0,0,0,16,3.85a1.53,1.53,0,0,1,2.88.43,3.51,3.51,0,0,0,4.89,2.64A1.53,1.53,0,0,1,25.74,9.1a3.45,3.45,0,0,0-.24,3.08,3.49,3.49,0,0,0,2.28,2.09,1.52,1.52,0,0,1,.08,2.9Z" />
              <path d="M19.3,11.29,13,17.59V14.67a1,1,0,0,0-2,0V20a1,1,0,0,0,.07.38,1.06,1.06,0,0,0,.55.55,1.07,1.07,0,0,0,.76,0,1.21,1.21,0,0,0,.32-.22l8-8a1,1,0,1,0-1.41-1.41Z" />
            </g>
          </svg>
          <span class={style.label}>Validate</span>
          <span class={style.ball_pulse}>
            <span />
            <span />
            <span />
          </span>
        </button>
      </div>
      <Error_MSG result={validateError} />
      <Success_MSG result={validateMSG} />

      <p class={style.footer}>
        Already did? <Link class={style.route} href="/login">Log in</Link>
      </p>
		</div>
	);
}

export default Validate;
